import React, { Component } from 'react';
import { Brain } from './item/brain';
import { Import } from './item/import';
// import { HDWallet } from './item/hd';
import { Save } from './item/save';


export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            accountComponentIndex: "0"
        }
        this.wallets = [
            <Save key="save" />,
            <Brain key="brain" />,
            <Import key="import" />,
            // <HDWallet />
        ];
    }
    changeWallet = (e) => {
        this.setState({ accountComponentIndex: e.target.value });
    }
    render() {
        return [
            <div key="choose" className="form-group">
                <select className="form-control" onChange={this.changeWallet} defaultValue={'0'}>
                    <option value="0">儲存錢包</option>
                    <option value="1">大腦錢包</option>
                    <option value="2">匯入私鑰</option>
                    {/* <option value="3"}>HD錢包</option> */}
                </select>
            </div>,
            this.wallets[this.state.accountComponentIndex]
        ]
    }
}

export { Login }