import {
  computed,
  action
} from 'mobx';
let object = {}
let array = []
class ObjectState {
  @computed get object() {
    return object;
  }
  @computed get array() {
    return array;
  }
  @action.bound setObject(_object) {
    object = Object.assign({}, _object)
  }
  @action.bound setArray(_array) {
    array = [..._array];
  }
}

export default ObjectState;
