import {
    observable,
    computed,
    action,
    autorun
} from 'mobx';
import * as bitcoinlib from 'bitcoinjs-lib';

const LITECOIN = {
    messagePrefix: '\x19Litecoin Signed Message:\n',
    bip32: {
      public: 0x019da462,
      private: 0x019d9cfe
    },
    pubKeyHash: 0x30,
    scriptHash: 0x32,
    wif: 0xb0
  }

export default class BitcoinStore {
    @observable litecoinAddress = '';
    @observable testnetAddress = '';
    @observable testnetSegwitAddress = '';
    @observable legacyAddress = '';
    @observable segwitAddress = '';
    constructor(accountStore) {
        autorun(() => {
            this.setAddress(accountStore.privateKeyHex);
        })
    }

    @action.bound setAddress(privateKeyHex) {
        let keyPair;
        if (!privateKeyHex) {
            this.litecoinAddress = '';
            this.testnetAddress = '';
            this.testnetSegwitAddress = '';
            this.legacyAddress = '';
            this.segwitAddress = '';
        } else {
            keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(privateKeyHex, 'hex'), { network: LITECOIN });
            this.litecoinAddress = bitcoinlib.payments.p2pkh({ pubkey: keyPair.publicKey, network: LITECOIN }).address


            keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(privateKeyHex, 'hex'), { network: bitcoinlib.networks.testnet });
            this.testnetAddress = bitcoinlib.payments.p2pkh({ pubkey: keyPair.publicKey, network: bitcoinlib.networks.testnet }).address

            keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(privateKeyHex, 'hex'), { network: bitcoinlib.networks.testnet });
            this.testnetSegwitAddress = bitcoinlib.payments.p2sh({
                network: bitcoinlib.networks.testnet,
                redeem: bitcoinlib.payments.p2wpkh({
                    pubkey: keyPair.publicKey,
                    network: bitcoinlib.networks.testnet
                })
            }).address;

            bitcoinlib.ECPair.fromPrivateKey(Buffer.from(privateKeyHex, 'hex'), { network: bitcoinlib.networks.bitcoin });
            this.legacyAddress = bitcoinlib.payments.p2pkh({ pubkey: keyPair.publicKey, network: bitcoinlib.networks.bitcoin }).address

            keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(privateKeyHex, 'hex'), { network: bitcoinlib.networks.bitcoin });
            this.segwitAddress = bitcoinlib.payments.p2sh({
                redeem: bitcoinlib.payments.p2wpkh({
                    pubkey: keyPair.publicKey
                })
            }).address;
        };
    }


}