import React, { Component, lazy } from 'react';
import { Route } from 'react-router-dom'
import PleaseLogin from '../pleaseLogin';
import Config from '../../config.json';


const Balance = lazy(()=>import('./general/balance'));
const OtherBalance = lazy(()=>import('./general/otherBalance'));
const Transfer = lazy(()=>import('./general/transfer'));
  



export default function BitcoinRoute(props) {
    return [
        <Route key="balance" exact path={Config.route.bitcoin.general.balance}
            component={() => (
                <PleaseLogin
                    accountState={props.accountState}
                    ethereumState={props.ethereumState}
                    langugeState={props.langugeState}
                >
                    <Balance
                        accountState={props.accountState}
                        bitcoinState={props.bitcoinState}
                    />
                </PleaseLogin>
            )}
        />,
        <Route key="transfer" exact path={Config.route.bitcoin.general.transfer} component={() => (
            <PleaseLogin
                accountState={props.accountState}
                ethereumState={props.ethereumState}
                langugeState={props.langugeState}
            >
                <Transfer
                    accountState={props.accountState}
                    bitcoinState={props.bitcoinState}
                />
            </PleaseLogin>
        )} />,
        <Route key="otherbalance" exact path={Config.route.bitcoin.general.otherBalance}
            component={() => (
                <OtherBalance
                    accountState={props.accountState}
                    bitcoinState={props.bitcoinState}
                />
            )}
        />,
    ]
}

export { BitcoinRoute };