import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Detail from './detail';
import Login from './login';

@inject('ethereumStore')
@inject('accountStore')
@observer
export default class AccountOfSetting extends Component {
    render() {
        let View = this.props.accountStore.privateKeyHex ? <Detail /> : <Login />
        return (
            <div className={"tab-pane active"} id="account-manage">
                <form onSubmit={(e) => { e.preventDefault() }}>
                    <h3 className="control-sidebar-heading">帳號管理</h3>
                    {View}
                </form>
            </div>
        )
    }
}