import React from 'react';
import ReactDom from 'react-dom';
import { IntlProvider } from 'react-intl';
import { HashRouter } from 'react-router-dom';
import { Provider } from 'mobx-react';
import App from './App';
import 'normalize.css';
import { AccountStore, BitcoinStore, EthereumStore } from './mobx'
import * as ethers from 'ethers/index.js';
ethers.utils.BigNumber.prototype.toJSON = function () {
    return this.toString()
}

const accountStore = new AccountStore();
const bitcoinStore = new BitcoinStore(accountStore);
const ethereumStore = new EthereumStore(accountStore);

ReactDom.render(
  <Provider
    accountStore={accountStore}
    bitcoinStore={bitcoinStore}
    ethereumStore={ethereumStore}
  >
    <IntlProvider locale="en">
      <HashRouter>
        <App />
      </HashRouter>
    </IntlProvider>
  </Provider>
  , document.getElementById('root')
)
