import React, { Component, lazy } from 'react';
import { Route, Switch } from 'react-router-dom';
import Config from '../../config.json';
import PleaseLogin from '../pleaseLogin';
import { ObjectStore } from '../../mobx';


const Balance = lazy(() => import('./multiSignWallet/balance'));
const Manage = lazy(() => import('./multiSignWallet/manage'));
const Transfer = lazy(() => import('./multiSignWallet/transfer'));
const EDSign = lazy(() => import('./electronicDocument/sign'));
const EDCreateContract = lazy(() => import('./electronicDocument/createContract'));
const EDManage = lazy(() => import('./electronicDocument/manage'));
const SendMail = lazy(() => import('./ethMail/sendMail'));
const Reading = lazy(() => import('./ethMail/reading'));
const Inbox = lazy(() => import('./ethMail/inbox'));
const Guess1000 = lazy(() => import('./game/1000guess'));
const WinOrLost = lazy(() => import('./game/winOrLose'));
const CreateProject = lazy(() => import('./voting/createProject'));
const AllProject = lazy(() => import('./voting/allProject'));
const VotingProject = lazy(() => import('./voting/votingProject'));



export default function Dapp(props) {
    let voting = new ObjectStore();
    return <Switch>
        <Route key="balance" exact path={Config.route.dapp.mutilSignWallet.balance} component={() => (
            <PleaseLogin>
                <Balance />
            </PleaseLogin>
        )} />
        <Route key="tokenManage" exact path={Config.route.dapp.mutilSignWallet.tokenManage} component={() => (
            <PleaseLogin>
                <Manage />
            </PleaseLogin>
        )} />
        <Route key="etherTransfer" exact path={Config.route.dapp.mutilSignWallet.etherTransfer} component={() => (
            <PleaseLogin>
                <Transfer />
            </PleaseLogin>
        )} />
        <Route key="tokenTransfer" exact path={Config.route.dapp.mutilSignWallet.tokenTransfer} component={() => (
            <PleaseLogin>
                <Transfer />
            </PleaseLogin>
        )} />
        <Route key="sign" exact path={Config.route.dapp.electonDocument.sign} component={() => (
            <PleaseLogin
                unlogin
                networkId='3'
            >
                {/* <Test /> */}
                <EDSign />
            </PleaseLogin>
        )} />
        <Route key="manage" exact path={Config.route.dapp.electonDocument.manage} component={() => (
            <PleaseLogin
                networkId='3'
            >
                <EDManage />
            </PleaseLogin>
        )} />
        
        <Route key="allProject" exact path={Config.route.dapp.voting.allProject} component={() => (
            <AllProject voting={voting} />
        )} />
        <Route key="createProject" exact path={Config.route.dapp.voting.createProject} component={() => (
            <PleaseLogin
                unlogin
                networkId='3'
            >
                <CreateProject />
            </PleaseLogin>
        )} />
        <Route key="project" path={Config.route.dapp.voting.project} component={() => (
            <PleaseLogin
                unlogin
                networkId='3'
            >
                <VotingProject voting={voting} />
            </PleaseLogin>
        )} />
        <Route key="inbox" exact path={Config.route.dapp.ethMail.inbox} component={() => (
            <PleaseLogin
                networkId='3'
            >
                <Inbox />
            </PleaseLogin>
        )} />
        <Route key="reading" exact path={Config.route.dapp.ethMail.reading} component={() => (
            <PleaseLogin
                networkId='3'
            >
                <Reading
                    ethMail={ethMail} />
            </PleaseLogin>
        )} />
        <Route key="sendMail" exact path={Config.route.dapp.ethMail.sendMail} component={() => (
            <PleaseLogin
                networkId='3'
            >
                <SendMail />
            </PleaseLogin>
        )} />
        <Route key="1000guess" exact path={Config.route.dapp.game['1000guess']} component={() => (
            <PleaseLogin
                unlogin
                networkId='3'
            >
                <Guess1000 />
            </PleaseLogin>
        )} />,
        <Route key="winOrLose" exact path={Config.route.dapp.game.winOrLose} component={() => (
            <PleaseLogin
                unlogin
                networkId='3'
            >
                <WinOrLost />
            </PleaseLogin>
        )} />
    </Switch>
}
export { Dapp }