import React, { Component } from 'react';
import * as bitcoinlib from 'bitcoinjs-lib';
import { observer, inject } from 'mobx-react';
// import { QRcodeView } from '../../../component/qrcode'
import QRCode from 'qrcode-react'



const LITECOIN = {
    messagePrefix: '\x19Litecoin Signed Message:\n',
    bip32: {
        public: 0x019da462,
        private: 0x019d9cfe
    },
    pubKeyHash: 0x30,
    scriptHash: 0x32,
    wif: 0xb0
}

@inject('bitcoinStore')
@inject('ethereumStore')
@inject('accountStore')
@observer
export default class Detail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            addressType: "ethereumAddress",
            showPrivateKey: false,
            privateKey: ''
        }
        this.count = 0;
    }

    logout = () => {
        this.props.accountStore.logout()
    }
    remove = () => {
        this.props.accountStore.remove()
    }

    showPrivateKey = () => {
        this.setState({ privateKey: '於3秒後顯示' });
        setTimeout(() => {
            this.setState({ showPrivateKey: true });
            setTimeout(() => {
                this.count++;
                this.setState({
                    privateKey: `已顯示過私鑰${this.count}次`,
                    showPrivateKey: false
                });
            }, 60000)
        }, 3000)
    }

    render() {
        let keyPair;
        if (this.state.showPrivateKey) {
            switch (this.state.addressType) {
                case "ethereumAddress":
                    this.state.privateKey = this.props.accountStore.privateKeyHex
                    break;
                case "segwitAddress":
                case "legacyAddress":
                    keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(this.props.accountStore.privateKeyHex, 'hex'), { network: bitcoinlib.networks.bitcoin });
                    this.state.privateKey = keyPair.toWIF()
                    break;
                case "testnetSegwitAddress":
                case "testnetAddress":
                    keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(this.props.accountStore.privateKeyHex, 'hex'), { network: bitcoinlib.networks.testnet });
                    this.state.privateKey = keyPair.toWIF()
                    break;
                case "litecoinAddress":
                    keyPair = bitcoinlib.ECPair.fromPrivateKey(Buffer.from(this.props.accountStore.privateKeyHex, 'hex'), { network: LITECOIN });
                    this.state.privateKey = keyPair.toWIF()
                    break;
            }
        }
        return ([
            <div key='1' className="form-group">
                <label className="control-sidebar-subheading">
                    當前地址
                </label>
                <select
                    className="form-control"
                    defaultValue="ethereumAddress"
                    onChange={(e) => {
                        this.setState({ addressType: e.target.value })
                    }}
                >
                    <option value="ethereumAddress">以太坊地址</option>
                    <option value="legacyAddress">比特幣(legacy)地址</option>
                    <option value="segwitAddress">比特幣(segwit)地址</option>
                    <option value="testnetAddress">比特幣測試鏈(legacy)地址</option>
                    <option value="testnetSegwitAddress">比特幣測試鍊(segwit)地址</option>
                    <option value="litecoinAddress">萊特幣地址</option>
                </select>
                <p style={{ "display": "inline-flex", "padding": "5px", "backgroundColor": "#FFFFFF" }}><QRCode size={190} value={this.props['ethereumAddress' === this.state.addressType ? 'ethereumStore' : 'bitcoinStore']['ethereumAddress' === this.state.addressType ? 'address' : this.state.addressType]} /></p>
                <p style={{ "overflow": "hidden" }}>{this.props['ethereumAddress' === this.state.addressType ? 'ethereumStore' : 'bitcoinStore']['ethereumAddress' === this.state.addressType ? 'address' : this.state.addressType]}</p>
            </div>,
            <div key='2' className="form-group">
                <button className="btn btn-primary" onClick={this.showPrivateKey}>顯示私鑰</button>
                {
                    this.state.showPrivateKey && <p style={{ "display": "inline-flex", "padding": "5px", "backgroundColor": "#FFFFFF" }}><QRCode size={190} key={this.state.privateKey} value={this.state.privateKey} /></p>
                }
                <p style={{ "overflow": "hidden" }}>{this.state.privateKey}</p>
            </div>,
            <div key="3" className="pull-left"><button className="btn btn-primary" onClick={this.logout}>登出</button></div>,
            <div key="4" className="pull-right"><button className="btn btn-danger" onClick={this.remove}>移除帳號</button></div>

        ])
    }
}