import React, { Component } from 'react';
import { observer, inject } from 'mobx-react'
import InputPassword from '../../../../component/password';

@inject('accountStore')
@observer
export default class Save extends Component {
    constructor(props) {
        super(props);
        this.state = {
            accounts: JSON.parse(localStorage.getItem('accounts') || '[]')
        }
        this.accounts = {};
        this.state.accounts.forEach((v) => {
            this.accounts[v.sha256] = v;
        })
    }
    submit = (e) => {
        this.props.accountStore.saveLogin(this.accounts[this.sha256], this.state.password);
    }
    render() {
        let options = this.state.accounts.map(x => <option key={x.sha256} value={x.sha256}>{x.name} {x.address && `- ${x.address}`}</option>)
        return [
            <div key={1} className="form-group">
                <label className="control-label">帳號</label>
                <select className="form-control" onChange={(e) => { this.sha256 = e.target.value }}>
                    <option>請選擇</option>
                    {options}
                </select>
            </div>,
            <div key={2} className="form-group">
                <label className="control-label">密碼</label>
                <InputPassword
                    onChange={(e) => { this.setState({ password: e.target.value }) }}
                />
            </div>,
            <button key={3} className="btn btn-primary" onClick={this.submit}>確認</button>
        ]
    }
}

export { Save };