import React, { Component } from 'react';
import { observer, inject } from 'mobx-react'
import QRCodeInput from '../../../../component/qrcode/qrcodeInput';
import InputPassword from '../../../../component/password';

@inject('accountStore')
@observer
export default class Import extends Component {
    constructor(props) {
        super(props);
        this.state = {
            remember: true,
            privateKey: ''
        };
        this.submit = this.submit.bind(this);
    }

    submit = (e) => {
        if (/0x\w{64}/.test(this.state.privateKey)) {
            this.state.privateKey = this.state.privateKey.substr(2);
        }
        if (this.state.privateKey.length != 64) {
            alert('私鑰長度不正確')
        } else {
            this.props.accountStore.importLogin(this.state.privateKey, this.state.password, this.state.remember, this.state.name);
        }
    }

    render() {
        return (
            [
                this.state.remember && <div className="form-group">
                    <label className="control-label">名稱</label>
                    <input
                        type="email"
                        className="form-control"
                        placeholder="Name"
                        value={this.state.name}
                        onChange={(e) => { this.setState({ name: e.target.value }) }}
                    />
                </div>,
                <div className="form-group" key="privateKey">
                    <label className="control-label">私鑰</label>
                    {/* <input
                        type="password"
                        className="form-control"
                        placeholder="PrivateKey"
                        value={this.state.privateKey}
                        onChange={(e) => { this.setState({ privateKey: e.target.value }) }}
                    /> */}
                    <QRCodeInput
                        type="password"
                        placeholder="PrivateKey"
                        value={this.state.privateKey}
                        onChange={(e) => { this.setState({ privateKey: e.target.value }) }}
                    />
                </div>,
                this.state.remember && <div className="form-group">
                    <label className="control-label">密碼</label>
                    {/* <div className="input-group">
                        <input
                            type="password"
                            className="form-control"
                            inputMode="numeric"
                            placeholder="Password"
                            value={this.state.password}
                            onChange={(e) => { this.setState({ password: e.target.value }) }} />
                        <label className="input-group-addon" ><i className="fa fa-eye-slash"></i></label>
                    </div> */}
                    <InputPassword
                        onChange={(e) => { this.setState({ password: e.target.value }) }}
                    />
                </div>,
                <div className="form-group">
                    <div className="checkbox">
                        <label>
                            <input type="checkbox" checked={this.state.remember} onChange={(e) => { this.setState({ remember: e.target.checked }) }} /> 記住我
                    </label>
                    </div>
                </div>,
                <button className="btn btn-primary" onClick={this.submit}>確認</button>
            ]
        )
    }
}

export { Import };