import React, { Component, lazy } from 'react';
import { Route } from 'react-router-dom'
import Config from '../../config.json'


const UTF8Convert = lazy(() => import('./tool/utf8'));
const PrivateKeyTransfer = lazy(() => import('./tool/privateKeyTransfer'));
const QRcode = lazy(() => import('./tool/qrcode'));
const AbiToHumanReadable = lazy(() => import('./tool/abiToHumanReadable'));

export default function OtherRoute() {
    return [
        <Route key="utf8convert" exact path={Config.route.other.tool.utf8} component={() => <UTF8Convert />} />,
        <Route key="privateKeyTransfer" exact path={Config.route.other.tool.privateKeyTransfer} component={() => <PrivateKeyTransfer />} />,
        <Route key="qrcode" exact path={Config.route.other.tool.qrcode} component={() => <QRcode />} />,
        <Route key="abiToHumanReadable" exact path={Config.route.other.tool.abiToHumanReadable} component={() => <AbiToHumanReadable />} />,
    ]
}

export { OtherRoute }