import utils from 'ethereumjs-util';
import aesjs from 'aes-js';

function encode(text, pw) {
  let pwhashBytes = (utils.sha256(pw));
  let textBytes = aesjs.utils.utf8.toBytes(text)

  //encrypt
  let aesCtr = new aesjs.ModeOfOperation.ctr(pwhashBytes, new aesjs.Counter(5));
  let encryptedBytes = aesCtr.encrypt(textBytes);
  let encryptedHex = aesjs.utils.hex.fromBytes(encryptedBytes);
  return encryptedHex;
}

function decode(encryptedHex, pw) {
  let pwhashBytes = (utils.sha256(pw));
  //decrypt
  let aesCtr = new aesjs.ModeOfOperation.ctr(pwhashBytes, new aesjs.Counter(5));
  let encryptedBytes = aesjs.utils.hex.toBytes(encryptedHex);
  let textBytes = aesCtr.decrypt(encryptedBytes);

  // Convert our bytes back into text
  let text = aesjs.utils.utf8.fromBytes(textBytes);
  return text;
}

export {
  encode,
  decode
};
