import React, { Component } from 'react';
import qrcode from './qrcode';
import QrcodeReader from 'react-qr-reader';
import './camera.css'



function phone() {

	var iDevices = [
		'iPad Simulator',
		'iPhone Simulator',
		'iPod Simulator',
		'iPad',
		'iPhone',
		'iPod',
		'Android'
	];

	if (!!navigator.platform) {
		while (iDevices.length) {
			if (navigator.platform === iDevices.pop()) { return true; }
		}
	}

	return false;
}

export default class QRcodeInput extends Component {
	constructor(props) {
		super(props);
		this.state = {
			value: '',
			scanner: false,
		}
	}

	componentWillUnmount = () => {

	}

	openQRCamera = (e) => {
		if (phone()) {
			let reader = new FileReader();
			qrcode.callback = (res) => {
				if (res instanceof Error) {
					alert("No QR code found. Please make sure the QR code is within the camera's frame and try again.");
				} else {
					this.props.onChange({ target: { value: res } });
					this.forceUpdate();
				}
			};
			reader.onload = () => {
				qrcode.decode(reader.result);
			};
			reader.readAsDataURL(e.target.files[0]);
		} else {
			this.setState({ scanner: !this.state.scanner })
		}
	}

	render() {
		return (
			<div className="input-group">
				<input className="form-control"
					defaultValue={this.props.value}
					value={this.state.value}
					type={this.props.type}
					placeholder={this.props.placeholder}
					onChange={(e) => {
						this.setState({ value: e.target.value, scanner: false })
						this.props.onChange(e)
					}} />
				<label className="input-group-addon" >
					{
						phone() ?
							(
								<input type="file" accept="image/*" capture="environment"
									onChange={this.openQRCamera}
									tabIndex="-1" style={{ display: 'none' }} />
							) : (
								<input type="button" onClick={this.openQRCamera} tabIndex="-1" style={{ display: 'none' }} />
							)
					}
					<i className="fa fa-qrcode"></i>
				</label>
				<div className="camera">
					{this.state.scanner && <QrcodeReader
						onClick={() => {
							this.setState({ scanner: false })
						}}
						delay={300}
						onError={console.log}
						onScan={(value) => {
							if (value) {
								this.setState({ value, scanner: false })
								this.props.onChange({ target: { value } })
							}
						}}
						style={{ width: '100%' }}
					/>}
				</div>
			</div>
		)
	}
}
