import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import { observer, inject } from 'mobx-react';




function phone() {

    var iDevices = [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod',
        'Android'
    ];

    if (!!navigator.platform) {
        while (iDevices.length) {
            if (navigator.platform === iDevices.pop()) { return true; }
        }
    }

    return false;
}

@inject('ethereumStore')
@observer
export default class Header extends Component {
    componentDidMount = () => {
        document.querySelector('.navbar-custom-menu').addEventListener('click', (e) => {
            if (phone()) {
                e.stopPropagation()
                document.querySelector('.control-sidebar-dark').classList.toggle('control-sidebar-open')
                e.cancelBubble = true
                return false
            }
        }, false)
        // if (document.querySelector('[data-toggle="control-sidebar"]').onclick) {
        //     document.querySelector('[data-toggle="control-sidebar"]').click()
        // }
    }
    render() {
        let title = this.props.ethereumStore.address ? this.props.ethereumStore.address.substr(0, 10) : '登入'
        return (
            <header className="main-header">
                <Link className="logo" to="/home">
                    <span className="logo-mini">台灣證鏈科技</span>
                    <span className="logo-lg">台灣證鏈科技</span>
                </Link>
                <nav className="navbar navbar-static-top">
                    <a className="sidebar-toggle" data-toggle="push-menu" role="button">
                        <span className="sr-only">伸縮鈕</span>
                    </a>
                    <div className="navbar-custom-menu">
                        <ul className="nav navbar-nav">
                            <li>
                                <a data-toggle="control-sidebar">{title} <i className="fa fa-gears"></i></a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
        )
    }
}


export { Header };