import React, { Component } from 'react';
import { observer, inject } from 'mobx-react'
import InputPassword from '../../../../component/password';

@inject('accountStore')
@observer
export default class Brain extends Component {
    constructor(props) {
        super(props);
        this.state = {
            remember: true
        };
        this.submit = this.submit.bind(this);
    }

    submit = (e) => {
        this.props.accountStore.brainLogin(this.state.email, this.state.password, this.state.remember, this.state.name);
    }

    render() {
        return (
            [
                this.state.remember && <div className="form-group">
                    <label className="control-label">名稱</label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Name"
                        value={this.state.name}
                        onChange={(e) => { this.setState({ name: e.target.value }) }}
                    />
                </div>,
                <div className="form-group" key="email">
                    <label className="control-label">帳號</label>
                    <div className="input-group">
                        <input
                            type="email"
                            className="form-control"
                            placeholder="Email"
                            value={this.state.email}
                            onChange={(e) => { this.setState({ email: e.target.value }) }}
                        />
                        <label className="input-group-addon">
                            <i class="fa fa-user"></i>
                        </label>
                    </div>
                </div>,
                <div className="form-group" key="password">
                    <label className="control-label">密碼</label>
                    <InputPassword
                        onChange={(e) => { this.setState({ password: e.target.value }) }}
                    />
                </div>,
                <div className="form-group">
                    <div className="checkbox">
                        <label>
                            <input type="checkbox" checked={this.state.remember} onChange={(e) => { this.setState({ remember: e.target.checked }) }} /> 記住我
                    </label>
                    </div>
                </div>,
                <button className="btn btn-primary" onClick={this.submit}>確認</button>
            ]
        )
    }
}

export { Brain };