import {
    observable,
    computed,
    action,
    autorun
} from 'mobx';
import * as ethers from 'ethers/index.js';




export default class EthereumStore {
    @observable wallet;
    @observable provider = ethers.getDefaultProvider(parseInt(localStorage.getItem('eth_net') || 3));
    @observable networkId = 3;
    @observable address = '';
    constructor(accountStore) {
        autorun(() => {
            this.setWallet(accountStore.privateKeyHex);
        })
    }
    @action.bound setWallet(privateKeyHex) {
        if (!privateKeyHex) {
            this.address = ''
        } else {
            this.wallet = new ethers.Wallet(privateKeyHex);
            this.address = this.wallet.address;
        }

    }

    @action.bound setProvider(id) {
        localStorage.setItem('eth_net', id)
        this.provider = ethers.getDefaultProvider(id);
        this.networkId = id;
    }
}