import {
    observable,
    computed,
    action,
    autorun,
    isObservable,
    observe
} from 'mobx';
import utils from 'ethereumjs-util';
import { encode, decode } from '../lib/passwordEDcode'
import * as ethers from 'ethers/index.js';


export default class AccountStore {
    @observable privateKeyHex = '';

    @action.bound logout() {
        this.privateKeyHex = '';
    }

    @action.bound remove() {
        this.logout();
        let accounts = JSON.parse(localStorage.getItem('accounts') || '[]');
        accounts = accounts.filter((x) => x.sha256 !== this.sha256);
        localStorage.setItem('accounts', JSON.stringify(accounts));
    }

    @action.bound brainLogin(email, password, remember, name = '') {
        this.privateKeyHex = toPrivateKey(email, password);
        if (remember) this.save(this.privateKeyHex, password, name);
    }

    @action.bound importLogin(privateKey, password, remember, name = '') {
        this.privateKeyHex = privateKey, password;
        if (remember) this.save(this.privateKeyHex, password, name);
    }

    @action.bound saveLogin(account = { encode: '' }, password) {
        let privateKeyHex = decode(account.encode, password);
        if (account.sha256 !== utils.sha256(privateKeyHex).toString('hex')) {
            this.privateKeyHex = '';
            alert('密碼錯誤');
        } else {
            this.sha256 = account.sha256;
            this.privateKeyHex = privateKeyHex;
        }
    }

    save(privateKey, password, name) {
        let accounts = JSON.parse(localStorage.getItem('accounts') || '[]');
        accounts.push({
            sha256: utils.sha256(privateKey).toString('hex'),
            name,
            encode: encode(privateKey, password),
            address: new ethers.Wallet(privateKey).address.substr(0,10)
        })
        localStorage.setItem('accounts', JSON.stringify(accounts));
    }


}

function toPrivateKey(email, pass) {
    let s = email;
    s += '|' + pass + '|';
    s += s.length + '|!@' + ((pass.length * 7) + email.length) * 7;
    let regchars = (pass.match(/[a-z]+/g)) ? pass.match(/[a-z]+/g).length : 1;
    let regupchars = (pass.match(/[A-Z]+/g)) ? pass.match(/[A-Z]+/g).length : 1;
    let regnums = (pass.match(/[0-9]+/g)) ? pass.match(/[0-9]+/g).length : 1;
    s += ((regnums + regchars) + regupchars) * pass.length + '3571';
    s += (s + '' + s);

    for (let i = 0; i <= 50; i++) {
        s = utils.sha256(s);
    }
    let privateKey = utils.sha256(s);

    return privateKey.toString('hex');
}
