import React, { Component, lazy } from 'react';
import { Route } from 'react-router-dom'
import Config from '../../config.json';
// import { Balance, OtherBalance, Transfer } from './Loadables/Loadables'
// import Balance from './general/balance';
// import OtherBalance from './general/otherBalance';
// import Transfer from './general/transfer';


const Welcome = lazy(() => import('./welcome'));
const About = lazy(() => import('./about'));





export default function WelcomeRoute(props) {
    return [
        <Route key="home" exact path={Config.route.home}
            component={() => { return <Welcome /> }} >
        </Route>,
        <Route key="about" exact path={Config.route.about}
            component={() => { return <About /> }} >
        </Route>
    ]
}

export {
    WelcomeRoute
}